export default "// description: El clásico juego de 4 en Raya con animaciones y efectos visuales\n// categories: Estrategia, Clásicos, Multijugador\nimport React, { useState, useEffect } from \"react\";\nimport { Card, CardContent, CardHeader, CardTitle } from \"@/components/ui/card\";\n\nconst ROWS = 6;\nconst COLS = 7;\n\nconst styles = {\n  board: {\n    backgroundColor: \"#0055b3\",\n    borderRadius: \"10px\",\n    padding: \"15px\",\n    display: \"grid\",\n    gridTemplateColumns: `repeat(${COLS}, 1fr)`,\n    gap: \"10px\",\n    boxShadow: \"0 0 10px rgba(0, 0, 0, 0.3)\",\n  },\n  column: {\n    display: \"flex\",\n    flexDirection: \"column\",\n  },\n  cell: {\n    width: \"50px\",\n    height: \"50px\",\n    backgroundColor: \"#f0f0f0\",\n    borderRadius: \"50%\",\n    margin: \"5px\",\n    cursor: \"pointer\",\n    transition: \"background-color 0.3s\",\n  },\n  red: {\n    backgroundColor: \"#ff4136\",\n    animation: \"drop 0.5s\",\n  },\n  yellow: {\n    backgroundColor: \"#ffdc00\",\n    animation: \"drop 0.5s\",\n  },\n  columnSelector: {\n    width: \"100%\",\n    height: \"20px\",\n    backgroundColor: \"transparent\",\n    cursor: \"pointer\",\n    borderRadius: \"5px\",\n    transition: \"background-color 0.3s\",\n  },\n  columnSelectorHover: {\n    backgroundColor: \"rgba(255, 255, 255, 0.3)\",\n  },\n  controls: {\n    marginTop: \"20px\",\n    display: \"flex\",\n    gap: \"10px\",\n  },\n  button: {\n    padding: \"10px 20px\",\n    border: \"none\",\n    borderRadius: \"5px\",\n    backgroundColor: \"#0055b3\",\n    color: \"white\",\n    cursor: \"pointer\",\n    fontSize: \"16px\",\n    transition: \"background-color 0.3s\",\n  },\n  buttonHover: {\n    backgroundColor: \"#003d82\",\n  },\n  status: {\n    fontSize: \"18px\",\n    fontWeight: \"bold\",\n    margin: \"15px 0\",\n    height: \"25px\",\n  },\n  winning: {\n    animation: \"pulse 1s infinite\",\n  },\n};\n\nconst FourInARow = () => {\n  const [gameState, setGameState] = useState(\n    Array(COLS)\n      .fill()\n      .map(() => Array(ROWS).fill(0))\n  );\n  const [currentPlayer, setCurrentPlayer] = useState(1);\n  const [gameActive, setGameActive] = useState(true);\n  const [lastMove, setLastMove] = useState(null);\n  const [winningCells, setWinningCells] = useState([]);\n\n  const makeMove = (col) => {\n    if (!gameActive) return;\n\n    const columnArray = [...gameState[col]];\n    const rowIndex = columnArray.findIndex((cell) => cell === 0);\n\n    if (rowIndex === -1) return;\n\n    const newGameState = gameState.map((column) => [...column]);\n    newGameState[col][rowIndex] = currentPlayer;\n    setGameState(newGameState);\n    setLastMove({ row: rowIndex, col });\n\n    if (checkWin(newGameState, rowIndex, col)) {\n      setGameActive(false);\n      setWinningCells(findWinningCells(newGameState, rowIndex, col));\n      return;\n    }\n\n    if (checkDraw(newGameState)) {\n      setGameActive(false);\n      return;\n    }\n\n    setCurrentPlayer(currentPlayer === 1 ? 2 : 1);\n  };\n\n  const checkWin = (board, row, col) => {\n    const player = board[col][row];\n    let count;\n\n    // Check horizontal\n    count = 1;\n    for (let c = col - 1; c >= 0 && board[c][row] === player; c--) count++;\n    for (let c = col + 1; c < COLS && board[c][row] === player; c++) count++;\n    if (count >= 4) return true;\n\n    // Check vertical\n    count = 1;\n    for (let r = row - 1; r >= 0 && board[col][r] === player; r--) count++;\n    if (count >= 4) return true;\n\n    // Check diagonal (bottom-left to top-right)\n    count = 1;\n    for (\n      let r = row - 1, c = col - 1;\n      r >= 0 && c >= 0 && board[c][r] === player;\n      r--, c--\n    )\n      count++;\n    for (\n      let r = row + 1, c = col + 1;\n      r < ROWS && c < COLS && board[c][r] === player;\n      r++, c++\n    )\n      count++;\n    if (count >= 4) return true;\n\n    // Check diagonal (bottom-right to top-left)\n    count = 1;\n    for (\n      let r = row - 1, c = col + 1;\n      r >= 0 && c < COLS && board[c][r] === player;\n      r--, c++\n    )\n      count++;\n    for (\n      let r = row + 1, c = col - 1;\n      r < ROWS && c >= 0 && board[c][r] === player;\n      r++, c--\n    )\n      count++;\n    if (count >= 4) return true;\n\n    return false;\n  };\n\n  const findWinningCells = (board, row, col) => {\n    const player = board[col][row];\n    const winningCells = [];\n    let count, tempWinningCells;\n\n    // Check horizontal\n    count = 1;\n    tempWinningCells = [{ row, col }];\n    for (let c = col - 1; c >= 0 && board[c][row] === player; c--) {\n      count++;\n      tempWinningCells.push({ row, col: c });\n    }\n    for (let c = col + 1; c < COLS && board[c][row] === player; c++) {\n      count++;\n      tempWinningCells.push({ row, col: c });\n    }\n    if (count >= 4) winningCells.push(...tempWinningCells);\n\n    // Check vertical\n    count = 1;\n    tempWinningCells = [{ row, col }];\n    for (let r = row - 1; r >= 0 && board[col][r] === player; r--) {\n      count++;\n      tempWinningCells.push({ row: r, col });\n    }\n    if (count >= 4) winningCells.push(...tempWinningCells);\n\n    // Check diagonal (bottom-left to top-right)\n    count = 1;\n    tempWinningCells = [{ row, col }];\n    for (\n      let r = row - 1, c = col - 1;\n      r >= 0 && c >= 0 && board[c][r] === player;\n      r--, c--\n    ) {\n      count++;\n      tempWinningCells.push({ row: r, col: c });\n    }\n    for (\n      let r = row + 1, c = col + 1;\n      r < ROWS && c < COLS && board[c][r] === player;\n      r++, c++\n    ) {\n      count++;\n      tempWinningCells.push({ row: r, col: c });\n    }\n    if (count >= 4) winningCells.push(...tempWinningCells);\n\n    // Check diagonal (bottom-right to top-left)\n    count = 1;\n    tempWinningCells = [{ row, col }];\n    for (\n      let r = row - 1, c = col + 1;\n      r >= 0 && c < COLS && board[c][r] === player;\n      r--, c++\n    ) {\n      count++;\n      tempWinningCells.push({ row: r, col: c });\n    }\n    for (\n      let r = row + 1, c = col - 1;\n      r < ROWS && c >= 0 && board[c][r] === player;\n      r++, c--\n    ) {\n      count++;\n      tempWinningCells.push({ row: r, col: c });\n    }\n    if (count >= 4) winningCells.push(...tempWinningCells);\n\n    return winningCells;\n  };\n\n  const checkDraw = (board) => {\n    for (let col = 0; col < COLS; col++) {\n      if (board[col][ROWS - 1] === 0) return false;\n    }\n    return true;\n  };\n\n  const resetGame = () => {\n    setGameState(\n      Array(COLS)\n        .fill()\n        .map(() => Array(ROWS).fill(0))\n    );\n    setCurrentPlayer(1);\n    setGameActive(true);\n    setLastMove(null);\n    setWinningCells([]);\n  };\n\n  const getStatusMessage = () => {\n    if (!gameActive) {\n      if (winningCells.length > 0) {\n        return `¡Jugador ${currentPlayer} (${\n          currentPlayer === 1 ? \"Rojo\" : \"Amarillo\"\n        }) ha ganado!`;\n      }\n      return \"¡Empate!\";\n    }\n    return `Turno del Jugador ${currentPlayer} (${\n      currentPlayer === 1 ? \"Rojo\" : \"Amarillo\"\n    })`;\n  };\n\n  return (\n    <div className=\"w-full max-w-3xl mx-auto p-4\">\n      <Card className=\"mb-4\">\n        <CardHeader className=\"bg-blue-500 text-white\">\n          <CardTitle className=\"text-center text-2xl\">4 en Raya</CardTitle>\n        </CardHeader>\n        <CardContent>\n          <div style={styles.status}>{getStatusMessage()}</div>\n\n          <div style={styles.board}>\n            {Array(COLS)\n              .fill()\n              .map((_, col) => (\n                <div key={col} style={styles.column}>\n                  <div\n                    style={styles.columnSelector}\n                    onClick={() => makeMove(col)}\n                  />\n                  {Array(ROWS)\n                    .fill()\n                    .map((_, row) => {\n                      const value = gameState[col][ROWS - 1 - row];\n                      const isWinning = winningCells.some(\n                        (cell) =>\n                          cell.row === ROWS - 1 - row && cell.col === col\n                      );\n\n                      return (\n                        <div\n                          key={`${col}-${row}`}\n                          style={{\n                            ...styles.cell,\n                            ...(value === 1 && styles.red),\n                            ...(value === 2 && styles.yellow),\n                            ...(isWinning && styles.winning),\n                          }}\n                        />\n                      );\n                    })}\n                </div>\n              ))}\n          </div>\n\n          <div style={styles.controls}>\n            <button style={styles.button} onClick={resetGame}>\n              Reiniciar Juego\n            </button>\n          </div>\n        </CardContent>\n      </Card>\n\n      <style>\n        {`\n          @keyframes drop {\n            from {transform: translateY(-300px);}\n            to {transform: translateY(0);}\n          }\n          @keyframes pulse {\n            0% {transform: scale(1);}\n            50% {transform: scale(1.1);}\n            100% {transform: scale(1);}\n          }\n        `}\n      </style>\n    </div>\n  );\n};\n\nexport default FourInARow;\n";