export default "// description: Un juego educativo donde los jugadores deben encontrar palabras que empiecen con una letra específica y compartir datos interesantes sobre ellas\n// categories: Educativo, Lenguaje, Escolar\nimport React, { useState, useEffect } from \"react\";\nimport { Card, CardContent, CardHeader, CardTitle } from \"@/components/ui/card\";\n\nconst WordExplorerGame = () => {\n  // Estados para manejar el juego\n  const [gameState, setGameState] = useState(\"setup\"); // setup, playing, results\n  const [currentRound, setCurrentRound] = useState(1);\n  const [totalRounds, setTotalRounds] = useState(3);\n  const [timeLeft, setTimeLeft] = useState(30);\n  const [currentCategory, setCurrentCategory] = useState(\"\");\n  const [currentLetter, setCurrentLetter] = useState(\"\");\n  const [word, setWord] = useState(\"\");\n  const [fact, setFact] = useState(\"\");\n  const [score, setScore] = useState(0);\n  const [answers, setAnswers] = useState([]);\n  const [teams, setTeams] = useState([{ name: \"Equipo 1\", score: 0 }]);\n  const [activeTeam, setActiveTeam] = useState(0);\n  const [timer, setTimer] = useState(null);\n  const [maxTime, setMaxTime] = useState(30);\n\n  // Categorías y letras disponibles\n  const categories = [\n    \"Animales\",\n    \"Países\",\n    \"Comidas\",\n    \"Deportes\",\n    \"Profesiones\",\n    \"Objetos de casa\",\n  ];\n  const letters = \"ABCDEFGHIJLMNOPQRST\".split(\"\");\n\n  // Iniciar una nueva ronda\n  const startNewRound = () => {\n    // Seleccionar categoría y letra al azar\n    const randomCategory =\n      categories[Math.floor(Math.random() * categories.length)];\n    const randomLetter = letters[Math.floor(Math.random() * letters.length)];\n\n    setCurrentCategory(randomCategory);\n    setCurrentLetter(randomLetter);\n    setTimeLeft(maxTime);\n    setWord(\"\");\n    setFact(\"\");\n    setGameState(\"playing\");\n\n    // Iniciar temporizador\n    const interval = setInterval(() => {\n      setTimeLeft((prevTime) => {\n        if (prevTime <= 1) {\n          clearInterval(interval);\n          endRound();\n          return 0;\n        }\n        return prevTime - 1;\n      });\n    }, 1000);\n\n    setTimer(interval);\n  };\n\n  // Finalizar la ronda actual\n  const endRound = () => {\n    if (timer) clearInterval(timer);\n\n    if (currentRound >= totalRounds) {\n      setGameState(\"results\");\n    } else {\n      setGameState(\"setup\");\n      setCurrentRound((prev) => prev + 1);\n    }\n  };\n\n  // Enviar una respuesta\n  const submitAnswer = () => {\n    if (!word.trim() || !fact.trim()) return;\n\n    // Calcular puntos basados en el tiempo restante\n    const timeBonus = Math.floor((timeLeft / maxTime) * 100);\n    const points = 50 + timeBonus;\n\n    // Guardar la respuesta\n    const newAnswer = {\n      word: word.trim(),\n      fact: fact.trim(),\n      timeBonus,\n      points,\n      category: currentCategory,\n      letter: currentLetter,\n    };\n\n    setAnswers((prev) => [...prev, newAnswer]);\n\n    // Actualizar puntuación del equipo\n    const updatedTeams = [...teams];\n    updatedTeams[activeTeam].score += points;\n    setTeams(updatedTeams);\n\n    // Limpiar campos\n    setWord(\"\");\n    setFact(\"\");\n\n    // Cambiar al siguiente equipo si hay más de uno\n    if (teams.length > 1) {\n      setActiveTeam((prev) => (prev + 1) % teams.length);\n    }\n  };\n\n  // Reiniciar el juego\n  const resetGame = () => {\n    setGameState(\"setup\");\n    setCurrentRound(1);\n    setScore(0);\n    setAnswers([]);\n    setTeams(teams.map((team) => ({ ...team, score: 0 })));\n  };\n\n  // Agregar un equipo\n  const addTeam = () => {\n    if (teams.length < 4) {\n      setTeams((prev) => [\n        ...prev,\n        { name: `Equipo ${prev.length + 1}`, score: 0 },\n      ]);\n    }\n  };\n\n  // Limpiar temporizador cuando el componente se desmonte\n  useEffect(() => {\n    return () => {\n      if (timer) clearInterval(timer);\n    };\n  }, [timer]);\n\n  return (\n    <div className=\"w-full max-w-3xl mx-auto p-4\">\n      <Card className=\"mb-4\">\n        <CardHeader className=\"bg-blue-500 text-white\">\n          <CardTitle className=\"text-center text-2xl\">\n            El Explorador de Palabras\n          </CardTitle>\n        </CardHeader>\n        <CardContent>\n          {gameState === \"setup\" && (\n            <div className=\"space-y-4 p-4\">\n              <h2 className=\"text-xl font-bold text-center mb-6\">\n                Configuración\n              </h2>\n\n              <div className=\"flex justify-between items-center mb-4\">\n                <span className=\"font-medium\">Número de rondas:</span>\n                <div className=\"flex items-center\">\n                  <button\n                    className=\"px-3 py-1 bg-gray-200 rounded-l\"\n                    onClick={() =>\n                      setTotalRounds((prev) => Math.max(1, prev - 1))\n                    }\n                  >\n                    -\n                  </button>\n                  <span className=\"px-4 py-1 bg-gray-100\">{totalRounds}</span>\n                  <button\n                    className=\"px-3 py-1 bg-gray-200 rounded-r\"\n                    onClick={() => setTotalRounds((prev) => prev + 1)}\n                  >\n                    +\n                  </button>\n                </div>\n              </div>\n\n              <div className=\"flex justify-between items-center mb-4\">\n                <span className=\"font-medium\">Tiempo por ronda (seg):</span>\n                <div className=\"flex items-center\">\n                  <button\n                    className=\"px-3 py-1 bg-gray-200 rounded-l\"\n                    onClick={() => setMaxTime((prev) => Math.max(10, prev - 5))}\n                  >\n                    -\n                  </button>\n                  <span className=\"px-4 py-1 bg-gray-100\">{maxTime}</span>\n                  <button\n                    className=\"px-3 py-1 bg-gray-200 rounded-r\"\n                    onClick={() => setMaxTime((prev) => prev + 5)}\n                  >\n                    +\n                  </button>\n                </div>\n              </div>\n\n              <div className=\"mb-6\">\n                <h3 className=\"font-medium mb-2\">Equipos:</h3>\n                {teams.map((team, index) => (\n                  <div key={index} className=\"flex items-center mb-2\">\n                    <input\n                      type=\"text\"\n                      value={team.name}\n                      onChange={(e) => {\n                        const newTeams = [...teams];\n                        newTeams[index].name = e.target.value;\n                        setTeams(newTeams);\n                      }}\n                      className=\"border rounded p-2 flex-grow\"\n                    />\n                    <span className=\"ml-2\">Puntos: {team.score}</span>\n                  </div>\n                ))}\n                {teams.length < 4 && (\n                  <button\n                    onClick={addTeam}\n                    className=\"mt-2 px-4 py-2 bg-blue-500 text-white rounded\"\n                  >\n                    Añadir equipo\n                  </button>\n                )}\n              </div>\n\n              <div className=\"text-center\">\n                <button\n                  onClick={startNewRound}\n                  className=\"px-6 py-3 bg-green-500 text-white rounded-lg font-bold\"\n                >\n                  {currentRound === 1 ? \"¡Empezar juego!\" : \"Siguiente ronda\"}\n                </button>\n              </div>\n            </div>\n          )}\n\n          {gameState === \"playing\" && (\n            <div className=\"space-y-4 p-4\">\n              <div className=\"flex justify-between mb-2\">\n                <span>\n                  Ronda: {currentRound}/{totalRounds}\n                </span>\n                <span className=\"font-bold\">\n                  Turno: {teams[activeTeam].name}\n                </span>\n              </div>\n\n              <div className=\"bg-blue-100 p-4 rounded-lg mb-4 text-center\">\n                <h2 className=\"text-xl mb-2\">\n                  Categoría: <strong>{currentCategory}</strong>\n                </h2>\n                <h2 className=\"text-2xl\">\n                  Letra:{\" \"}\n                  <strong className=\"text-3xl text-blue-600\">\n                    {currentLetter}\n                  </strong>\n                </h2>\n              </div>\n\n              <div className=\"flex justify-center mb-4\">\n                <div className=\"text-center p-2 bg-red-100 rounded-full w-16 h-16 flex items-center justify-center\">\n                  <span className=\"text-2xl font-bold\">{timeLeft}</span>\n                </div>\n              </div>\n\n              <div className=\"space-y-3\">\n                <div>\n                  <label className=\"block text-sm font-medium mb-1\">\n                    Palabra que empieza con '{currentLetter}':\n                  </label>\n                  <input\n                    type=\"text\"\n                    value={word}\n                    onChange={(e) => setWord(e.target.value)}\n                    className=\"w-full border rounded p-2\"\n                    placeholder={`Escribe un ${currentCategory.toLowerCase()} con ${currentLetter}...`}\n                  />\n                </div>\n\n                <div>\n                  <label className=\"block text-sm font-medium mb-1\">\n                    Dato interesante:\n                  </label>\n                  <textarea\n                    value={fact}\n                    onChange={(e) => setFact(e.target.value)}\n                    className=\"w-full border rounded p-2\"\n                    placeholder=\"Escribe un dato interesante sobre esta palabra...\"\n                    rows=\"3\"\n                  />\n                </div>\n\n                <div className=\"flex space-x-4 justify-center pt-2\">\n                  <button\n                    onClick={submitAnswer}\n                    className=\"px-4 py-2 bg-green-500 text-white rounded\"\n                    disabled={!word.trim() || !fact.trim()}\n                  >\n                    Enviar respuesta\n                  </button>\n\n                  <button\n                    onClick={endRound}\n                    className=\"px-4 py-2 bg-red-500 text-white rounded\"\n                  >\n                    Terminar ronda\n                  </button>\n                </div>\n              </div>\n\n              <div className=\"mt-4\">\n                <h3 className=\"font-medium mb-2\">Respuestas en esta ronda:</h3>\n                <ul className=\"space-y-2\">\n                  {answers\n                    .filter(\n                      (a) =>\n                        a.category === currentCategory &&\n                        a.letter === currentLetter\n                    )\n                    .map((answer, i) => (\n                      <li key={i} className=\"border-l-4 border-blue-500 pl-2\">\n                        <strong>{answer.word}</strong> - {answer.fact}\n                        <div className=\"text-sm text-gray-600\">\n                          Bonus por tiempo: +{answer.timeBonus} pts | Total:{\" \"}\n                          {answer.points} pts\n                        </div>\n                      </li>\n                    ))}\n                </ul>\n              </div>\n            </div>\n          )}\n\n          {gameState === \"results\" && (\n            <div className=\"space-y-4 p-4\">\n              <h2 className=\"text-xl font-bold text-center mb-6\">\n                ¡Fin del juego!\n              </h2>\n\n              <div className=\"mb-6\">\n                <h3 className=\"font-medium mb-2\">Puntuación final:</h3>\n                {teams\n                  .sort((a, b) => b.score - a.score)\n                  .map((team, index) => (\n                    <div\n                      key={index}\n                      className={`p-2 mb-2 rounded ${\n                        index === 0\n                          ? \"bg-yellow-100 border-l-4 border-yellow-400\"\n                          : \"\"\n                      }`}\n                    >\n                      {index === 0 && (\n                        <span className=\"text-yellow-700 font-bold\">\n                          🏆 GANADOR:{\" \"}\n                        </span>\n                      )}\n                      <span className=\"font-medium\">{team.name}</span>:{\" \"}\n                      {team.score} puntos\n                    </div>\n                  ))}\n              </div>\n\n              <div className=\"mb-6\">\n                <h3 className=\"font-medium mb-2\">Todas las respuestas:</h3>\n                {categories\n                  .filter((cat) => answers.some((a) => a.category === cat))\n                  .map((category) => (\n                    <div key={category} className=\"mb-4\">\n                      <h4 className=\"text-blue-600 mb-1\">{category}:</h4>\n                      <ul className=\"pl-4\">\n                        {answers\n                          .filter((a) => a.category === category)\n                          .map((answer, i) => (\n                            <li key={i} className=\"mb-1\">\n                              <strong>{answer.word}</strong> ({answer.letter}) -{\" \"}\n                              {answer.fact} ({answer.points} pts)\n                            </li>\n                          ))}\n                      </ul>\n                    </div>\n                  ))}\n              </div>\n\n              <div className=\"text-center\">\n                <button\n                  onClick={resetGame}\n                  className=\"px-6 py-3 bg-blue-500 text-white rounded-lg font-bold\"\n                >\n                  Jugar de nuevo\n                </button>\n              </div>\n            </div>\n          )}\n        </CardContent>\n      </Card>\n    </div>\n  );\n};\n\nexport default WordExplorerGame;\n";